<template>
  <div class="course-card">
    <div class="course-image" v-if="course.image">
      <img :src="`${baseUrl}${course.image}`" :alt="course.name" loading="lazy">
      <div class="age-range">{{ course.age_range }}</div>
    </div>
    <div class="course-content">
      <span class="level-badge" :class="levelClass">{{ course.level }}</span>
      <p class="name"><strong>{{ course.name }}</strong></p>
      <p class="hours">{{ course.hours }} giờ học</p>
      <p class="desc">{{ course.desc }}</p>
      <div class="course-footer">
        <router-link :to="'/courses/' + course.id" class="btn btn-primary">Xem chi tiết</router-link>
        <span class="price-tag" v-if="course.price">{{ new Intl.NumberFormat('vi-VN').format(course.price) }} VND</span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'

const props = defineProps({
  course: Object
})

const baseUrl = ref(process.env.VUE_APP_BASE_URL || '')

const levelClass = computed(() => {
  const lvl = (typeof props.course?.level === 'string' ? props.course.level.toLowerCase() : '')
  if (lvl.includes('pre') || lvl.includes('a1')) return 'lvl-light'
  if (lvl.includes('a2') || lvl.includes('ket')) return 'lvl-blue'
  if (lvl.includes('b1') || lvl.includes('pet')) return 'lvl-green'
  return 'lvl-blue'
})
</script>

<style scoped>
.course-card {
  background: white;
  border-left: 6px solid var(--blue-900);
  border-radius: 8px;
  box-shadow: 0 6px 16px rgba(11, 42, 111, 0.12);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.course-card:hover {
  transform: translateY(-6px) scale(1.01);
  box-shadow: 0 10px 24px rgba(11, 42, 111, 0.18);
}

.course-image {
  position: relative;
  height: 180px; /* Giảm chiều cao ảnh */
  overflow: hidden;
}

.course-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.age-range {
  position: absolute;
  top: 8px;
  right: 8px;
  background: var(--accent-red);
  color: white;
  padding: 2px 7px; /* Giảm padding */
  border-radius: 12px;
  font-size: 0.75rem; /* Giảm font-size */
  font-weight: bold;
}

.course-content {
  padding: 10px; /* Giảm padding */
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.level-badge {
  display: inline-block;
  border-radius: 999px;
  padding: 6px 10px;
  font-size: 0.85rem;
  font-weight: 700;
  margin-bottom: 6px;
}
.level-badge.lvl-light { background: var(--blue-100); color: var(--blue-900); }
.level-badge.lvl-blue { background: var(--blue-600); color: #fff; }
.level-badge.lvl-green { background: #10b981; color: #fff; }

.name {
  font-size: 1.05rem; /* Giảm font-size */
  color: #333;
  margin-bottom: 4px; /* Giảm margin */
  line-height: 1.25;
  font-weight: 600;
}

.hours {
  color: #666;
  font-size: 0.8rem; /* Giảm font-size */
  margin-bottom: 6px; /* Giảm margin */
}

.desc {
  color: #555;
  line-height: 1.35;
  margin-bottom: 10px; /* Giảm margin */
  flex-grow: 1;
  font-size: 0.8rem;
}

.course-footer {
  margin-top: auto;
  padding-top: 8px; /* Giảm padding */
  border-top: 1px solid #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn { font-size: 0.9rem; }

.price-tag {
  font-size: 0.95rem;
  background: var(--accent-yellow);
  color: #111827;
  padding: 6px 10px;
  border-radius: 8px;
  font-weight: 800;
}

@media (max-width: 768px) {
  .course-image {
    height: 140px;
  }
  
  .course-content {
    padding: 8px;
  }
  
  .name {
    font-size: 1rem;
  }
  
  .course-footer {
    flex-direction: column;
    gap: 6px;
    align-items: flex-start;
  }
}
</style>